<template>
  <div style="width:500px">
    <h2>:ok</h2>
    <el-form ref="form" :model="form" label-width="140px">
      <el-form-item label="">
        <span v-focusable @click="numberClick()" @on-blur="numberblur()" class="inline-block"><el-input v-model="form.name"></el-input></span>
      </el-form-item>
      <el-form-item label="">
        <div v-focusable @click="dialogopen()">{{form.more}}</div>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-focusable v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="form.type">
          <el-checkbox v-focusable label="/" name="type"></el-checkbox>
          <el-checkbox v-focusable label="" name="type"></el-checkbox>
          <el-checkbox v-focusable label="" name="type"></el-checkbox>
          <el-checkbox v-focusable label="" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="focusableradio">
        <el-radio-group v-model="form.focusableresource">
          <span class="inline-block" v-focusable @click="rediofocus()"><el-radio label=""></el-radio></span>
          <span class="inline-block" v-focusable @click="rediofocus()"><el-radio v-focusable label=""></el-radio></span>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="elementradio">
        <el-radio-group v-model="form.resource">
          <el-radio v-focusable label=""></el-radio>
          <el-radio v-focusable label=",ok"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <span v-focusable @click="textareaClick()" @on-blur="textareablur()" class="inline-block">
          <el-input type="textarea" v-model="form.desc"></el-input>
        </span>
      </el-form-item>
      <el-form-item>
        <el-button v-focusable type="primary" @click="onSubmit"></el-button>
        <el-button v-focusable @click="onCancel"></el-button>
      </el-form-item>
    </el-form>


    <div class="dialog" v-if="vocationshow">
		  <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
          <el-button v-focusable style="float: right; padding: 3px 0" type="text" @click="choosevocation()"></el-button>
        </div>
        <div class="cont" style="height:150px;overflow:hidden;">
          <div v-focusable 
            :class="'item item'+item.value" 
            v-for="item in vocations" 
            :key="item.value" 
            @click="choosevocation(item)">
            {{item.name}}
          </div>
        </div>
      </el-card>
	  </div>
  </div>

</template>
<script>
export default {
  name: "Example9",
  data() {
    return {
      form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          focusableresource:'',
          desc: '',
          more:'ok'
        },
        oldFocusEl:null,
        vocationshow:false,
        vocations:[
          {name:'',value:'1'},
          {name:'',value:'2'},
          {name:'',value:'3'},
          {name:'',value:'4'},
          {name:'',value:'5'},
          {name:'',value:'6'},
          {name:'1',value:'61'},
          {name:'2',value:'62'},
          {name:'3',value:'63'},
          {name:'4',value:'64'},
          {name:'5',value:'65'},
          {name:'6',value:'66'},
          {name:'7',value:'67'},
          {name:'8',value:'68'},
          {name:'9',value:'69'},
          {name:'10',value:'610'},
          {name:'11',value:'611'},
        ],
    };
  },
  created() {
   this.$nextTick(() => {
     // element enter ,focusable,AWDSP 
      this.$tv.KEYS = {
        KEY_LEFT: [65, 37], // 65:A  37:
        KEY_UP: [87, 38], // 87:W  38:
        KEY_RIGHT: [68, 39], // 68:D   39:
        KEY_DOWN: [83, 40], // 83S  40:
        KEY_ENTER: [80, 13], // 32: P  13enter
      };
   })
  },
  destroyed() {
   
  },

  methods: {
    rediofocus(){
      document.querySelector('.focus input[type=radio]').click()
    },
    numberblur(){
      document.querySelector('.focus input').blur()
    },
    numberClick(){
      document.querySelector('.focus input').focus()
    },
    textareaClick(){
      document.querySelector('.focus textarea').focus()
    },
    textareablur(){
      document.querySelector('.focus textarea').blur()
    },


    
    onSubmit() {
      alert('submit!');
    },
    onCancel() {
      alert('onCancel!');
    },
    choosevocation(item){ 
      if(item){this.form.more = item.name;}
			this.vocationshow = false;
			this.$tv.scrollEl = null; // 
			this.$tv.limitingEl = null; // 
				
			if (this.oldFocusEl) { this.$tv.requestFocus(this.oldFocusEl) } //  dialog 
		},
		dialogopen(){
			this.vocationshow = true;
			this.oldFocusEl = document.querySelector(`.${this.$tv.focusClassName}`); // 
			this.$nextTick(() => {
				this.$tv.limitingEl = document.querySelector(".dialog"); // .demo2focusable
				this.$tv.setScrollEl(document.querySelector(".cont"))
				this.$tv.requestFocus(
					 document.querySelector(".item1")
				);
			})
		}
  },
};
</script>


<style lang="less" scoped>
  .focus{box-shadow:0 0 5px #00a0e9;}
  .inline-block{display:inline-block}
	.dialog{position: fixed;top:0;bottom:0;left:0;right:0;z-index:99;background-color: rgba(0,0,0,0.5);align-items: center;justify-content: center;}
	.box-card{
		position: absolute;left:50%;top:50%;margin-top:-250px;margin-left:-250px;
		width: 320px;
	}
	// .content .tabs{width:300px;padding:10px 0;}
	.box-card .item{height: 30px;}
 .box-card .focus{box-shadow:none;font-size: 16px;color:#FF6600;}
</style>
